import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { IToolbarButtonConfig, SuperscriptButton } from '@ali/4ever-component';
import { PluginRoles } from '@ali/4ever-bamboo';
import { toggleSup } from '../../actions';
import { SHORTCUT } from '../../utils';
;
;
;

export interface SupButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: Record<string, string>;
}

export default function SupButton(props: SupButtonProps) {
  const { controller, locale, tooltip: tooltipProp, ...rest } = props;
  const isActive = controller.query('isSup');
  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = isCollapsedAtEdge
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInHr')
    || controller.query('isSelectionInMention')
    || controller.query('isSelectionInLink')
    || controller.query('isSelectionInListSymbol')
    || controller.query('isSelectionInLinkCard');

  const handleClick = React.useCallback(() => controller.run('onAction', toggleSup()), [controller]);

  const tooltip = React.useMemo(() => ({
    title: locale?.supButtonTitle || '',
    shortCut: SHORTCUT.sup,
    description: `Markdown: ^${locale?.text || 'Text'}^ ${locale?.space || 'Space'}`,
    ...tooltipProp,
  }), [tooltipProp]);

  return (
    <SuperscriptButton
      testid="bi-toolbar-sup"
      active={isActive}
      disabled={!!isDisabled}
      onClick={handleClick}
      role={PluginRoles.vertAlignSup}
      title={locale?.supButtonTitle}
      tooltip={tooltip}
      {...rest}
    />
  );
}
